  .range-wrapper__input {
    -webkit-appearance: none;
    width: 64%;
    height: 2px;
    background: #283645;
}

.range-wrapper__input::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 12px;
    height: 12px;
    border: 2px solid #d84c72;
    border-radius: 50%;
    background-color: #f2f2f2;
    cursor: pointer;
}

.range-wrapper__input::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #f2f2f2;
    cursor: pointer;
}

